.menu-item {
  font-weight: bold;
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  overflow: hidden;


  .background-image {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    transform: scale(1);
    transition: all 1.5s cubic-bezier(0.25, 0.45, 0.45, 0.95);
  }

  &:hover {
    cursor: pointer;
  
    & .background-image {
      transform: scale(1.2);
    }
  
    & .content {
      opacity: 0.95;
    }
  }

  &.large {
    height: 360px;
  }

  &:first-child {
    margin-right: 7.5px;
  }

  &:last-child {
    margin-left: 7.5px;
  }

  .content {
    background-color: white;
    opacity: 0.7;
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    position: absolute;
    transition: opacity 0.3s ease-in-out;

    .title {
      margin-top: 0px;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a;
    }

    .subtitle {
      font-weight: lighter;
      font-size: 16px;
    }
  }
}